.backdrop {
	display: flex;
	align-items: center;
	justify-content: center;
	position: fixed;
	top: 0;
	left: 0;
	width: 100%;
	height: 100%;
	background-color: rgba(47, 47, 47, 0.6);
	opacity: 1;
	visibility: visible;
	pointer-events: initial;
	overflow: scroll;
	z-index: 999;
}

.modal {
	background-color: none;
}

.button {
	position: absolute;
	top: 30px;
	right: 40px;
	background-color: transparent;
}

.icon {
	stroke: #2F2F2F;
	width: 24px;
	height: 24px;

	@media screen and (min-width: 768px) {
		width: 28px;
		height: 28px;
	}
}